
import { defineComponent } from "vue";
import HisStandardForm from "@/components/Forms/HisStandardForm.vue";
import { IonPage } from "@ionic/vue"
import { Field } from "@/components/Forms/FieldInterface";
import PersonField from "@/utils/HisFormHelpers/PersonFieldHelper";
import { generateDateFields } from "@/utils/HisFormHelpers/MultiFieldDateHelper";
import { FieldType } from "@/components/Forms/BaseFormElements";
import { Offline, isOfflineMode } from '@/composables/offline'
import { PatientRegistrationService } from "@/services/patient_registration_service";
import { Patientservice } from "@/services/patient_service";
import { nextTask } from "@/utils/WorkflowTaskHelper";

export default defineComponent({
    components: {
        IonPage,
        HisStandardForm
    },
    data: () => ({
        fields: [] as Field[]
    }),
    created() {
        this.fields = [
            PersonField.getGenderField(),
            ...generateDateFields(PersonField.getDobConfig()),
            {
                ...PersonField.getGivenNameField(),
                type: FieldType.TT_HIDDEN,
                computedValue: () => ({ person: "Unknown" })
            },
            {
                ...PersonField.getFamilyNameField(),
                type: FieldType.TT_HIDDEN,
                computedValue: () => ({ person: "Unknown" })
            },
            {
                ...PersonField.getHomeDistrictField(),
                type: FieldType.TT_HIDDEN,
                computedValue: () => ({ person: "Unknown" })
            },
            {
                ...PersonField.getHomeTaField(),
                type: FieldType.TT_HIDDEN,
                computedValue: () => ({ person: "Unknown" })

            },
            {
                ...PersonField.getHomeVillageField(),
                type: FieldType.TT_HIDDEN,
                computedValue: () => ({ person: "Unknown" })

            },
            {
                ...PersonField.getCurrentDistrictField(),
                type: FieldType.TT_HIDDEN,
                computedValue: () => ({ person: "Unknown" })
            },
            {
                ...PersonField.getCurrentTAfield(),
                type: FieldType.TT_HIDDEN,
                computedValue: () => ({ person: "Unknown" })
            },
            {
                ...PersonField.getCurrentVillageField(),
                type: FieldType.TT_HIDDEN,
                computedValue: () => ({ person: "Unknown" })

            },
            {
                ...PersonField.getCellNumberField(),
                type: FieldType.TT_HIDDEN,
                computedValue: () => ({ person: "Unknown" })
            },
        ]
    },
    methods: {
        async onFinish(_: any, computedData: any) {
            let personID = -1
            const person = PersonField.resolvePerson(computedData);
            if (!isOfflineMode.value) {
                const registration: any = new PatientRegistrationService();
                new Patientservice((await registration.registerBasicPatient(person)));
                personID = registration.getPersonID()
            } else {
                const { savePersonOffline } = Offline()
                personID = savePersonOffline('Professional test registration',person, [])
            }
            try {
                nextTask(personID, this.$router, this.$route)
            } catch (e) {
                return this.$router.push({ path: `/patient/dashboard/${personID}` })
            }
        }
    }
})
